import React, { Component } from "react";
import FormPengambilanBarcode from "./Component/FormPengambilanBarcode";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Skeleton from "react-loading-skeleton";
import SelectSearch from "react-select-search";
import Swal from "sweetalert2";
import { getListDataPengambilanBarcode } from "../actions/pengambilanBarcodeAction";
import { connect } from "react-redux";
import { reset } from "redux-form";

const maptostate = (state) => {
  return {
    pengambilanBarcode: state.provinsi.pengambilanBarcode,
  };
};
class PengambilanBarcod extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "pabrik",
          text: "Pabrik",
        },
        {
          dataField: "barcode",
          text: "Barcode",
        },
        {
          dataField: "gross",
          text: "Gross",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar",
          text: "Kadar",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
      ],
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    this.props.dispatch(getListDataPengambilanBarcode());
  }
  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      kodeJenis: split[0],
      keterangan: data.keterangan,
      jenis: data.jenis,
      barcode: data.barcode,
      pkg: data.pkg,
      bruto: data.bruto,
      netto: data.netto,
      gross: data.gross,
      kadar: data.kadar,
      kadarTransaksi: data.kadarTransaksi,
      rp: data.rp,
    };
    var array = JSON.parse(localStorage.getItem("pengambilanBarcode")) || [];
    array.push(simpan);
    localStorage.setItem("pengambilanBarcode", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListDataPengambilanBarcode());
    this.props.dispatch(reset("FormPengambilanBarcode"));
    document.getElementById("dismissModal").click();
  }

  pengambilanBarcodeSubmit() {
    var simpan = {
      jenisTransaksi: this.state.jenisTransaksi,
      tanggal: this.state.tanggal,
      baki: this.state.baki,
      namaToko: this.state.namaToko,
      detail: this.state.detail,
      barang: JSON.parse(localStorage.getItem("pengambilanBarcode")),
    };
    console.log(simpan);
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Jenis Transaksi</label>
                      <select
                        type="text"
                        id="jenisTransaksi"
                        name="jenisTransaksi"
                        className="form-control"
                        onChange={this.inputChange("jenisTransaksi")}
                      >
                        <option value="RETURN SUPPLIER" key="RETURN SUPPLIER">
                          RETURN SUPPLIER
                        </option>
                        <option
                          value="PENERIMAAN SUPPLIER"
                          key="PENERIMAAN SUPPLIER"
                        >
                          PENERIMAAN SUPPLIER
                        </option>
                        <option value="PENJUALAN" key="PENJUALAN">
                          PENJUALAN
                        </option>
                        <option value="PINDAH BARANG" key="PINDAH BARANG">
                          PINDAH BARANG
                        </option>
                        <option value="RETURN CUSTOMER" key="RETURN CUSTOMER">
                          RETURN CUSTOMER
                        </option>
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                        onChange={this.inputChange("tanggal")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Baki</label>
                      <input
                        type="text"
                        id="baki"
                        name="baki"
                        className="form-control"
                        onChange={this.inputChange("baki")}
                      />
                    </div>
                    {this.state.jenisTransaksi === "PENJUALAN" ? (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksi === "PENERIMAAN SUPPLIER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksi === "RETURN SUPPLIER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksi === "PINDAH BARANG" ? (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Dari</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Ke</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksi === "RETURN CUSTOMER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    ) : (
                      <>
                        <div className="col-md-5 col-lg-4 mt-2">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.state.items}
                            search
                            placeholder="Silahkan Pilih"
                          />
                        </div>
                      </>
                    )}
                    <div className="col-lg-12 col-md-12 mt-4">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.pengambilanBarcodeSubmit()}
                            >
                              SIMPAN
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              data-target="#primary-header-modal"
                            >
                              TAMBAH DATA
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.pengambilanBarcode ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.pengambilanBarcode}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group mt-2">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <Skeleton width={"100%"} height={100} />
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Pengamblan Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormPengambilanBarcode
                  onSubmit={(data) => this.handleSubmit(data)}
                  noFaktur={this.props.noFaktur}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(PengambilanBarcod);
