import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import validationPengambilanBarcode from "../../Validations/validationPengambilanBarcode";
import { createNumberMask } from "redux-form-input-masks";
import Axios from "axios";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormPengambilanBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      DataSupplier: [],
      kodeBarang: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  pilihBarang = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarBeli: result_explode[3],
    });
  };

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("gross", this.props.gross);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 form-group">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeJenis"
              component="select"
              className="form-control"
              onChange={this.pilihBarang()}
              onBlur={this.setKadar()}
            >
              <option value="DEFUALT" key="DEFUALT">
                SILAHKAN PILIH
              </option>
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option
                    value={
                      kode.kodeJenis +
                      "|" +
                      kode.namaJenis +
                      "|" +
                      kode.kadarCetak +
                      "|" +
                      kode.kadarBeli
                    }
                  >
                    {kode.namaJenis}
                  </option>
                ))
              )}
            </Field>
          </div>
          <div className="col-md-8">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3">
            <label htmlFor="">.</label>
            <div className="row">
              <div className="col-md-6">
                <label>
                  <Field
                    name="jenis"
                    id="jenis"
                    component="input"
                    type="checkbox"
                  />
                  Manual
                </label>
              </div>
            </div>
          </div>
          <div className="col-md-3">
            <Field
              name="barcode"
              label="Barcode"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          {this.props.jenis === true ? (
            <>
              <div className="col-md-3">
                <Field
                  name="pkg"
                  label="Bungkus(Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                />
              </div>
              <div className="col-md-3">
                <Field
                  name="bruto"
                  label="Berat (Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                />
              </div>
              <div className="col-md-3">
                <Field
                  name="gross"
                  label="Gross (Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                />
              </div>
            </>
          ) : (
            <>
              <div className="col-md-3">
                <Field
                  name="pkg"
                  label="Bungkus(Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                  readOnly={true}
                />
              </div>
              <div className="col-md-3">
                <Field
                  name="bruto"
                  label="Berat (Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                  readOnly={true}
                />
              </div>
              <div className="col-md-3">
                <Field
                  name="gross"
                  label="Gross (Gr)"
                  type="number"
                  component={renderField}
                  normalize={(value) => value && value.toUpperCase()}
                  onBlur={() => this.onclick1()}
                  readOnly={true}
                />
              </div>
            </>
          )}

          <div className="col-md-3">
            <Field
              name="kadar"
              label="Kadar"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="kadarTransaksi"
              label="Harga (%)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="netto"
              label="Murni (Gr)"
              type="number"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.submitting}
                type="submit"
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormPengambilanBarcode = reduxForm({
  form: "FormPengambilanBarcode",
  validate: validationPengambilanBarcode,
  enableReinitialize: true,
})(FormPengambilanBarcode);
const selector = formValueSelector("FormPengambilanBarcode"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, pkg, jenis } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg",
    "jenis"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    gross: parseFloat(bruto) + parseFloat(pkg),
    jenis: jenis,
  };
})(FormPengambilanBarcode);
